.gram-section{
	position: relative;
	padding-bottom: 30%;

	@media screen and (min-width: 768px){
		padding-bottom: 20%;
	}

	.at{
		position: relative;

		@media screen and (min-width: 768px){
			padding-bottom: 20%;
		}

		h2{
			text-align: center;
			font-family: futura;
			color: #000;
			text-transform: uppercase;
			font-weight: bold;
			margin-top: 14%;
			left: 30vw;
			font-size: 1.3em;
			position: absolute;
		}

		img{
			position: absolute;
			margin-left: 10%;
		}

	}


	.gram-images{
		display: flex;
		justify-content: center;
		flex-wrap: wrap;

		@media screen and (min-width: 1024px){
			margin: auto 25%;
			justify-content: flex-end;
		}


		img{
			margin: 1%;
			border: 1px solid #ee1271;
			width: 28%;
			// height: 28%;
			align-self: center;

			@media screen and (min-width: 768px){
				margin: 2%;
			}
		}
	}

}


footer{
	width: 100vw;
	background-image: url(../img/footer.png);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0 100%;
	height: 90px; 

	.footer-text{
		margin-left: 10%;

		a{
			padding-top: 36px;
			// float: left;
			display: inline-block; // sit next to each other in a row
			padding-right: 9%;
			text-decoration: none;
			color: #000;
			font-family: futura;
			font-weight: bold;

			&:last-child{
				padding-right: 0%;
			}

			@media screen and (min-width: 1024px){
				padding-right: 5%;
				font-size: 1.2em;
				display: inline-block; // sit next to each other in a row
			}
		}
	}
}



